import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  IconButton,
  styled,
  Typography,
} from "@mui/material";
import React from "react";
import { useState } from "react";
import { rollTable } from "./helpers";
import TableDialog from "./TableDialog";

const ExpandMore = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? "rotate(0deg)" : "rotate(180deg)",
  marginLeft: "auto",
  transition: theme.transitions.create("transform", {
    duration: theme.transitions.duration.shortest,
  }),
}));

export default function Table({ table }) {
  const [result, setResult] = useState("");
  const [tableOpen, setTableOpen] = useState(false);
  // View full list function
  // Cards?
  const getResult = () => {
    setResult(rollTable(table));
  };

  const handleTableDialogClose = () => {
    setTableOpen(false);
  };

  return (
    <Card>
      <CardHeader title={table.title} subheader={table.description} />
      {result && (
        <CardContent>
          <Typography variant="h6" color="secondary.light">
            {result}
          </Typography>
        </CardContent>
      )}
      <CardActions sx={{ mx: "auto", display: "flex", justifyContent: "center", maxWidth: 600 }}>
        <Button variant="outlined" onClick={() => setTableOpen(true)} sx={{ flex: 0.25 }}>
          View
        </Button>
        <Button
          variant="contained"
          onClick={getResult}
          sx={{ width: "50%", mx: "auto", my: 1, flex: 0.5 }}>
          Generate
        </Button>
        <Button
          variant="outlined"
          onClick={() => setResult("")}
          sx={{ flex: 0.25, boxSizing: "content-box" }}>
          Clear
        </Button>
      </CardActions>
      <TableDialog open={tableOpen} handleClose={handleTableDialogClose} table={table} />
    </Card>
  );
}
