import {
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  MenuItem,
  MenuList,
  Paper,
} from "@mui/material";
import React from "react";
import { Link } from "react-scroll";

const headers = [
  { id: "actions", name: "Actions & Attributes" },
  { id: "stress-trauma", name: "Stress & Trauma" },
  { id: "clocks", name: "Clocks" },
  { id: "the-devils-bargain", name: "The Devil's Bargain" },
  { id: "effect", name: "Effect" },
  { id: "consequences-and-harm", name: "Consequences and Harm" },
  { id: "resistance-and-armor", name: "Resistance and Armor" },
  { id: "Fortune Roll", name: "Fortune Roll" },
  { id: "gathering-information", name: "Gathering Information" },
  { id: "coin-and-stash", name: "Coin and Stash" },
  { id: "the-faction-game", name: "The Faction Game" },
  { id: "rep", name: "Rep", indent: true },
  { id: "advancement", name: "Advancement" },
  { id: "character-creation", name: "Character creation" },
  { id: "cohorts", name: "Cohorts" },
  { id: "the-score", name: "The Score" },
  { id: "planning-and-engagement", name: "Planning & Engagement", indent: true },
  { id: "flashbacks", name: "Flashbacks", indent: true },
  { id: "teamwork", name: "Teamwork", indent: true },
  { id: "payoff", name: "Payoff" },
  { id: "heat", name: "Heat", indent: true },
  { id: "entanglements", name: "Entanglements", indent: true },
  { id: "downtime", name: "Downtime" },
  { id: "downtime-activities", name: "Downtime activities", indent: true },
  { id: "vices", name: "Vices" },
  { id: "magnitude", name: "Magnitude" },
  { id: "rituals", name: "Rituals" },
  { id: "crafting", name: "Crafting" },
  { id: "inventing", name: "Inventing" },
];

export default function Navigation({ width }) {
  return (
    <Paper
      width={width}
      sx={{
        position: "sticky",
        overflow: "auto",
        top: 50,
        // left: 40,
        height: "calc(100vh - 100px)",
      }}>
      <MenuList component="nav">
        {headers.map((header) => (
          <Link key={header.id} to={header.id} spy smooth offset={-10}>
            <MenuItem sx={{ fontSize: 17, pl: header.indent && 4 }}>{header.name}</MenuItem>
          </Link>
        ))}
      </MenuList>
    </Paper>
  );
}
